.container {
    display: grid;
    /*grid-template-columns: 25% 200px 25%;*/
    /*grid-template-rows: 300px 150px;*/
    /*grid-template: filas / columnas ;*/
    /*fr*/
    /*grid-template: 300px 100px 100px / repeat(4, 1fr);*/
    /*grid-template: 300px 100px 100px / repeat(4, minmax(200px, 1fr));*/
    grid-template: 100px 1fr 150px/ 200px 1fr;
    /*grid-gap: filas columnas;*/
    grid-gap: 10px;
    height: 100vh;
    grid-template-areas: "header header" "left contenido" "footer footer";
    .header {
        grid-area : header; }
    .left {
        grid-area : left; }
    .contenido {
        grid-area : contenido; }
    .footer {
        grid-area : footer; }
    .item {
        background: $lightBlue;
        padding: rem(10px);
        border: rem(1px solid red);
        &:nth-of-type(4) {
            background: blue;
            overflow: auto;
            /*display: subgrid;*/
            /*display: inline-grid;*/
            display: grid;
            grid-template: 50px 50px 50px / 200px 200px 200px; }
        .item {
            background: yellow; } } }
